import listItem from '@/components/listItem'
import ShopBtn from '@/components/shopBtn'
import nodata from '@/components/noData'

export default {
  name: 'shopList',
  props: {
    itemList: Array,
    border: {
      type: Boolean,
      default: true
    },
    noData: {
      type: Object
    }
  },
  computed: {
    borderStyle () {
      if (this.border) {
        const Style = {
          'border-bottom': '1px solid #d8d8d8',
          'height': '1.333333rem'
        }
        return Style
      }
    }
  },
  data () {
    return {
    }
  },
  created () {
    // console.log(this.itemList)
  },
  components: {
    listItem,
    ShopBtn,
    nodata
  },
  methods: {

  }
}



