import request from '@/utils/request'
import { getToken } from '@/utils/auth'
var qs = require('qs') // 序列化参数

/**
 *首次 微信授权获取token
 * @param {*} postData 参数对象
 */
export function getWeixinToken (postData) {
  postData = postData || {}
  return request({
    url: '/Api/System/getWeixinToken',
    method: 'post',
    data: qs.stringify(postData)
  })
}
