import request from '@/utils/request'
import { getToken } from '@/utils/auth'
var qs = require('qs') // 序列化参数

/**
 *获取banner
 * @param {*} postData 参数对象
 */
export function getBannerList (postData) {
  postData = postData || {}
  const token = getToken()
  console.log(token)
  postData.token = token
  return request({
    url: '/Api/Product/getBannerList',
    method: 'post',
    data: qs.stringify(postData)
  })
}

/**
 *获取活动列表
 * @param {*} postData 参数对象
 */
export function getActiveProductList (postData) {
  postData = postData || {}
  const token = getToken()
  console.log(token)
  postData.token = token
  return request({
    url: '/Api/ProductClient/getActiveProductList',
    method: 'post',
    data: qs.stringify(postData)
  })
}



