export default {
  name: 'shopBtn',
  props: {
    btntext: {
      type: String,
      default: '按钮'
    },
    btnWidth: String,
    btnHeight: String,
    // border宽度
    lineWidth: {
      type: Number,
      default: 1
    },
    // 背景颜色
    defaultBgColor: {
      type: String,
      default: 'white'
    },
    // border颜色
    defaultBorderColor: {
      type: String,
      default: '#d8d8d8'
    },
    // 不可选
    disabled: {
      type: Boolean,
      default: false
    },
    // 不可选颜色
    disabledColor: {
      type: String,
      default: 'silver'
    },
    // 文字颜色
    fontColor: {
      type: String,
      default: '#666666'
    },
    // 文字大小
    fontSize: {
      type: String,
      default: '15px'
    },
    // 文字对齐方式
    fontalign: {
      type: String,
      default: 'center'
    }
  },
  computed: {
    btnStyle () {
      const Style = {
        // 'font-family': 'PingFang-SC-Regular',
        'letter-spacing': 0,
        'box-sizing': 'border-box',
        'width': this.btnWidth || '2.4rem',
        'height': this.btnHeight || '0.8rem',
        'border-radius': '4px',
        'background-color': this.disabled ? '#F7F7F7' : this.defaultBgColor,
        // 'border': '3px solid red',
        'border': this.disabled ? `${this.lineWidth}px solid rgba(0, 0, 0, 0.2)` : `${this.lineWidth}px solid ${this.defaultBorderColor}`,
        'color': this.disabled ? 'rgba(0, 0, 0, 0.3)' : this.fontColor,
        'font-size': this.fontSize,
        'text-align': this.fontalign,
        'line-height': this.btnHeight || '0.8rem',
        'text-overflow': 'ellipsis',
        'overflow': 'hidden',
        'display': 'block'
      }
      return Style
    }
  },
  data () {
    return {

    }
  },
  created () {

  },
  components: {

  },
  methods: {
  }
}



