
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
Tabbar,
TabbarItem
},
computed: {
selected () {
let path = this.$route.path
let index = 0
console.log(path)
switch (path) {
case '/trialOrder':
index = 0
break
case '/newProduct':
index = 1
break
case '/collage':
index = 2
break
case '/secKill':
index = 3
break
default:
index = 0
break
}
return index
}
},
data () {
return {
}
}
}



