
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
computed: {
selected () {
let path = this.$route.path
let index = 0
switch (path) {
case '/':
index = 0
break
case '/classify':
index = 1
break
case '/shoppingtrolley':
index = 2
break
case '/usercenter':
index = 3
break
default:
index = 0
break
}
return index
}
},
components: {
Tabbar,
TabbarItem
}
}



