import request from '@/utils/request'
import { getToken } from '@/utils/auth'
var qs = require('qs') // 序列化参数

/**
 *获取商品可用活动列表
 * @param {*} postData 参数对象
 */
export function getSKUActiveList (postData) {
  postData = postData || {}
  const token = getToken()
  postData.token = token
  return request({
    url: '/Api/ProductClient/getSKUActiveList',
    method: 'post',
    data: qs.stringify(postData)
  })
}

