export default {
  name: 'itemList',
  props: {
    listData: Array,
    border: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    borderstyle () {
      if (this.border) {
        const Style = {
          'border-bottom': '1px solid #d8d8d8'
        }
        return Style
      }
    }
  },
  data () {
    return {
      noData: false
    }
  },
  created () {
    if (this.listData.length < 1) {
      this.noData = true
    }
  },
  components: {

  },
  methods: {

  }
}



